<template>
  <section class="shop-cart-index">
    <mt-header :title="title" fixed>
      <mt-button slot="right" @click="toggleState" >{{rightText}}</mt-button>
    </mt-header>
    <div class="my-order-list shop-car-height">
      <div class="scroll-contaier-cart">
        <div class="order-dp" v-for="(item,key) in listproducts">
          <header class="my-order-list-storeName dis-flex">
            <div class="hi-life-list-checkbox no-border">
              <i @click="checkorder(item.storeId,item.products)" :class="{'checkbox-selected':selectArr[item.storeId]==undefined?false:item.products.length==selectArr[item.storeId].length}"></i>
            </div>
            <i class="is-shop"></i>{{item.storeName}}
          </header>
          <div class="shoping-contanier" v-for="(item2,key2) in item.products">
            <div class="hi-life-list-checkbox">
              <i @click="check(item.storeId,item2.skuId)" :class="{'checkbox-selected':selectArr[item.storeId]==undefined?false:selectArr[item.storeId].indexOf(item2.skuId)>=0}"></i></div>
            <div class="hi-life-list-item-img"><img :src="item2.imageUrl"></div>
            <div class="hi-life-list-item-content">
              <div class="hi-life-list-item-summary">{{item2.summary}}</div>
              <div class="hi-life-list-detail">
                <span class="sp-sales">{{item2.sellPrice}}</span>
                <span class="sp-count" v-if="!isEdit">x{{item2.amount}}</span>
                <span class="shop-oper" v-if="isEdit">
                        <i class="shiftSize" @click="shiftSize(item,item2.skuId,item2.amount)"></i>
                        <span>{{item2.amount}}</span>
                <i class="addSize" @click="addSize(item,item2.skuId,item2.amount)"></i>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    </div>
    <div class="my-collection-bottom settle-accounts-box" v-if="edit">
      <div class="hi-life-list-checkboxall"><i class="ischeckAll" @click="checkAll" :class="{'checkbox-selected':selectArrLength == listproductsLength && listproductsLength!=0}"></i></div>
      <span>全选</span>
      <div class="dele-btn" @click="topay" v-if="!isEdit">结算{{size}}</div>
      <div class="dele-btn" @click="dele" v-if="isEdit">删除{{size}}</div>
    </div>
  </section>
</template>

<script>
  import { getCartList, getShopCartAgin, deletCart ,updateAmount} from '../../service/getData'
  export default {
    data () {
      return {
        title: '购物车',
        fixed: true,
        routerName: 'custom',
        rightText: '编辑',
        isEdit: false,
        isBind: true,
        atcive: 'products',
        edit: true,
        selectArr: {},
        ischeckAll: false,
        count: 0,
        listproducts: [],
        listproductsLength: 0,
        selectArrObj: []
      }
    },
    created: function () {
      this.getCart()
    },
    filters: {
      formatInt: function (val) {
        return val.toString().substr(0, val.toString().indexOf('.'))
      },
      formatDecimal: function (val) {
        return val.toString().substr(val.toString().indexOf('.'))
      }
    },
    methods: {
      addSize: function (storeId, skuId, size) {
        // 增加数量
        let _this = this,key = 0
        if (size < 10000) {  // 单条操作
          updateAmount(skuId,++size)
            .then(res => {
              if(res.data.code === 1){
                _this.$toast('加载成功!')
                _this.getCart()
              }
            })
            .catch(function () {
              _this.$toast('加载信息失败!')
            })
        } else {  // 批量操作
          this.selectArrObj
          for (key in this.selectArrObj) {
            if (this.selectArrObj[key] === skuId) {
              this.selectArrObj[key].amount++
            }
          }
        }
      },
      shiftSize: function (storeId, skuId, size) {
        // 减少数量
        let _this = this
        if (size > 1) {
          updateAmount(skuId,--size)
            .then(res => {
              if(res.data.code === 1){
                _this.$toast('加载成功!')
                _this.getCart()
              }
            })
            .catch(function () {
              _this.$toast('加载信息失败!')
            })
        }
      },
      toggleState: function () {
        if (this.isEdit) {
          this.isEdit = false
          /* 批量操作   */
          this.updateCart()
          console.log(this.selectArrObj)
          this.rightText = '编辑'
          this.selectArr = {}
        } else {
          this.isEdit = -1
          this.rightText = '完成'
        }
      },
      parentEvent: function () {
        this.edit = true
      },
      topay: function () {
        /* 得到选中的商品的详情 */
        this.$router.push({
          name: 'submit',
          query: {
            cartList: JSON.stringify(this.getAllSelect())
          }
        })
      },
      check: function (orederId, skuId) {
        if (this.selectArr[orederId] !== undefined) {
          if (this.selectArr[orederId].indexOf(skuId) < 0) {
            this.$set(this.selectArr[orederId], this.selectArr[orederId].length, skuId)
          } else {
            this.selectArr[orederId].splice(this.selectArr[orederId].indexOf(skuId), 1)
          }
        } else {
          let arr = []
          arr.push(skuId)
          this.$set(this.selectArr, orederId, arr)
        }
        console.log(this.selectArr)
      },
      checkorder: function (orederId, products) {
        let key1 = 0
        let key = 0
        if (this.selectArr[orederId] === undefined) {
          console.log(1)
          this.$set(this.selectArr, orederId, [])
          for (key in products) {
            this.$set(this.selectArr[orederId], this.selectArr[orederId].length, products[key].skuId)
          }
        } else {
          if (this.selectArr[orederId].length === products.length) {
            this.selectArr[orederId] = []
          } else {
            for (key1 in products) {
              if (this.selectArr[orederId].indexOf(products[key1].skuId) < 0) {
                this.$set(this.selectArr[orederId], this.selectArr[orederId].length, products[key1].skuId)
              }
            }
          }
        }
        console.log(this.selectArr)
      },
      dele: function () {
        let _this = this
        deletCart(this.getSkuIds().join())
        .then(res => {
          if (res.data.code === 1) {
            _this.$toast('删除成功')
            _this.edit = false
            _this.getCart()
          }
        })
        .catch(() => {
          _this.$toast('删除失败')
        })
      },
      checkAll: function () {
        if (this.listproductsLength !== this.selectArrLength) {
          let key = 0
          let key1 = 0
          for (key in this.listproducts) {
            if (this.selectArr[this.listproducts[key].storeId] === undefined) {
              this.$set(this.selectArr, this.listproducts[key].storeId, [])
            }
            for (key1 in this.listproducts[key].products) {
              if (this.selectArr[this.listproducts[key].storeId].indexOf(this.listproducts[key].products[key1].skuId) < 0) {
                this.$set(this.selectArr[this.listproducts[key].storeId], this.selectArr[this.listproducts[key].storeId].length, this.listproducts[key].products[key1].skuId)
              }
            }
          }
        } else {
          this.selectArr = {}
        }
        console.log(this.selectArr)
      },
      getCart: function () {
        var _this = this
        let key = 0
        getCartList()
        .then(res => {
          _this.listproducts = res.data
          for (key in res.data) {
            _this.listproductsLength += res.data[key].products.length
          }
        })
        .catch(function () {
          _this.$toast('加载信息失败!')
        })
      },
      getSkuIds: function () {
        /* 获取所有的选中的商品id */
       let values, skuIdArr = [], key =0,tempArr =[]
       values = Object.values(this.selectArr)
       for (key in values){
         skuIdArr = tempArr.concat(values[key])
       }
       return skuIdArr
      },
      updateCart: function () {
        this.listproducts
        let key = 0, key1 = 0, selectArr = [], tempObj = {}
        selectArr = this.getSkuIds()
        for (key in this.listproducts) {
          for (key1 in this.listproducts[key].products) {
            if (selectArr.indexOf(this.listproducts[key].products[key1].skuId) >= 0) {
              tempObj = {}
              tempObj.skuId = this.listproducts[key].products[key1].skuId
              tempObj.amount = this.listproducts[key].products[key1].amount
              this.selectArrObj.push(tempObj)
            }
          }
        }
      },
      getAllSelect: function () {
        let selectArr1 = [], key = 0, keys,tempObj, values, key1 = 0, key2 = 0,tempArr = []
        keys = Object.keys(this.selectArr)
        values = Object.values(this.selectArr)
        for (key2 in values) {
          tempArr = tempArr.concat(values[key2])
        }
        console.log(tempArr)
        for (key in this.listproducts) {
          if (keys.indexOf(this.listproducts[key].storeId.toString()) >= 0) {
            tempObj = {}, tempObj.products = []
            tempObj.cartId = this.listproducts[key].cartId
            tempObj.storeId = this.listproducts[key].storeId
            tempObj.storeName = this.listproducts[key].storeName
          }
          for (key1 in this.listproducts[key].products) {
            if (tempArr.indexOf(this.listproducts[key].products[key1].skuId) >= 0) {  // 如果在循环的产品在选中的商品中加入
              tempObj.products.push(this.listproducts[key].products[key1])
            }
          }
          if (keys.indexOf(this.listproducts[key].storeId.toString()) >= 0) {
            selectArr1.push(tempObj)
          }
        }
        console.log(JSON.stringify(selectArr1))
        return selectArr1
      }
    },
    watch: {
      isBind: function (newValue, oldValue) {
        if (newValue) {
          this.atcive = 'products'
        } else {
          this.atcive = 'store'
        }
      }
    },
    computed: {
      selectArrLength: function () {
        let count = 0
        let key = 0
        let keys = Object.keys(this.selectArr)
        for (key in keys) {
          count += this.selectArr[keys[key]].length
        }
        this.count = count
        return count
      },
      size: function () {
        if (this.selectArrLength === 0) {
          return ''
        } else {
          return '(' + this.selectArrLength + ')'
        }
      }
    }
  }
</script>

<style>
  .shop-cart-index{
    height: 100%;
    overflow: hidden;
  }
  .shop-car-height {
    overflow: hidden;
    height: 100%;
    padding: .4rem 0 .43rem .1rem;
  }
  .scroll-contaier-cart {
    height: 100%;
    overflow: auto;
    padding-bottom: .2rem;
    -webkit-overflow-scrolling: touch;
  }
  .dis-flex {
    display: flex;
  }
  
  .no-border {
    border-bottom: none;
  }
  
  .shoping-contanier {
    /* position: absolute; */
    display: flex;
  }
  
  .shop-cart-index .settle-accounts-box {
    bottom: .55rem;
    z-index: 1;
    background-color: #FFFFFF;
  }
  
  .is-shop {
    margin: 0 .06rem;
    transform: translate(0, 100%);
  }
  .{
    background-color: #FFFFFF;
    padding-left: .15rem;
  }
  .shop-oper{
    position: absolute;
    display: inline-block;
    bottom: 0;
    right: 0;
    height: .21rem;
    line-height: .21rem;
  }
    .shop-oper span{
      vertical-align: top;
    }
  .addSize{
    display: inline-block;
    width: .21rem;
    height: .21rem;
    margin-left: .05rem;
    background: url(/static/image/cart_edit_add@3x.png);
    background-size: 100%;
  }
  .shiftSize{
    display: inline-block;
    width: .21rem;
    height: .21rem;
    margin-right: .05rem;
    background: url(/static/image/cart_edit_cut@3x.png);
    background-size: 100%;
  }
  .shop-cart-index .my-collection-bottom {
    height: .43rem;
  }
  .my-collection-bottom {
    position: absolute;
  }
</style>


